/*
* Device Viewer
*/
.dv {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 0px 0px 5px 5px;
  background-color: var(--vc-black-color);
}

.dv-content {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 4px var(--vc-primary-border-color);
  -webkit-box-shadow: 0 0 4px var(--vc-primary-border-color);
}

.dv-content.dv-processing {
  cursor: progress;
}

.dv-content.dv-portrait > img {
  height: 600px;
}

.dv-content.dv-landscape > img {
  width: 600px;
}

.dv-content.dv-hidden {
  visibility: hidden;
}

.dv-content.dv-disabled {
  opacity: 0.8;
  pointer-events: none;
}

.dv-selected-control {
  position: absolute;
  background: var(--vc-primary-fg-color);
  opacity: 0.3;
}

/* Progress bar */
.dv-pb-text{ 
  text-align: center;
  color: var(--vc-primary-bg-color);
  margin: -50px 20px 20px 20px;
  font-size: 18px;
  opacity: 0.8;
}

.dv-pb-container {
  background-color: var(--vc-secondary-bg-color);
  text-align: center;
  width: 228px;
  height: 6px;
  border-radius: 5px;
}

.dv-pb-content {
  background: var(--vc-accent-color);
  height: 6px; 
  width: 80px;
  border-radius: 5px;
  animation: increase 3s infinite;
}

@keyframes increase {
  0% {transform: translateX(0);}
  50% {transform: translateX(148px);} 
  100% {transform: translateX(0);} 
}

/* Keyboard controller */
.dv textarea {
  position: absolute;
  background-color: transparent;
  border: none;
  resize: none;
  width: 0px;
  height: 0px;
  overflow: hidden;
  z-index: -1;
  opacity: 0;
}

.dv-web  textarea {
  position: absolute;
  width: 200px;
  height: 20px;
  overflow: hidden;
  opacity: 0;
  border: none;
}

.card {
  background: var(--surface-card);
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.p-galleria .p-galleria-thumbnail-container {
  background : none!important;
}

.thumbnails {
  width: 100%;
}

/*Image footer caption*/

.p-galleria-footer {
  position: absolute;
  top: 79%;
  left: 19%;
  right: 50%;
}

.custom-galleria-footer {
  display: flex;
  align-items: center;
  color: #fff;
 
}
.custom-galleria-footer > button {
  background-color: transparent;
  color: #fff;
  border: 0 none;
  border-radius: 0;
  margin: 0.2rem 0;
}

.custom-galleria-footer .title-container > span {
  font-size: 0.9rem;
  padding-left: 0.829rem;
}
.custom-galleria-footer .title-container > span.title {
  font-weight: bold;
}

.p-galleria-thumbnail-item {
  padding: 5px;
}

.p-galleria-thumbnail-items {
  background: white;
}

.p-galleria-thumbnail-items[pr_id_1] .p-galleria-thumbnail-item {
  flex: none!important;
  width: 10%;
}

.p-galleria-thumbnail-wrapper {
  position: fixed;
  bottom: 0;
  width: 74%;
}

.thumbnail-images {
  width: 100%;
  height: 80px;
}

.p-galleria-item-wrapper {
  flex-direction : row!important;
  height: 31rem;
  justify-content: center;
  margin: auto;
  overflow: hidden;
}

@media screen and (min-width : 1900px){
  .p-galleria-item-wrapper { 
    height: 50rem;
  }
}

@media screen and (max-width : 1100px){
  .p-galleria-item-wrapper { 
    height: 50rem;
  }
}

.p-galleria-item-wrapper .max-image{ 
  width: 100%;
  height: 100%;
  object-fit: contain;
}





